.features-carousel {
	overflow: hidden !important;
	padding: 60px 0;

	>div {
		width: 100%;
	}

	.next-arrow {
		position: relative;
		z-index: 20;

		svg {
			position: absolute;
			top: -90px;
			left: 50%;
			transform: translateX(-50%);
			width: 25px;
			height: 25px;
			color: rgba($color: #a1a1a1, $alpha: 0.5);
			cursor: pointer;
		}
	}

	.prev-arrow {
		position: relative;
		z-index: 20;

		svg {
			position: absolute;
			bottom: -65px;
			left: 50%;
			transform: translateX(-50%);
			width: 25px;
			height: 25px;
			color: rgba($color: #a1a1a1, $alpha: 0.5);
			cursor: pointer;
		}
	}

	.feature-item {
		display: flex !important;

		.vertical--text {
			color: #f26c4f !important;
		}
	}

	.featured--item-desc p {
		height: 100% !important;
		display: flex !important;
		align-items: center !important;
	}
}

.featured-collection-container {
	width: 100%;

	h2.heading--title {
		position: relative;
		color: #FFFFFF !important;
		z-index: 50;
		font-family: "Montserrat", sans-serif;
    font-size: 36px;
    font-weight: 700;
    line-height: 1.5;
    margin-bottom: 22px;
		letter-spacing: 1px;
		text-transform: capitalize;
	}

	.heading--desc {
		position: relative;
		color: #FFFFFF !important;
		z-index: 50;
		font-family: "Montserrat", sans-serif;
		font-size: 18px;
    font-weight: 400;
    line-height: 28px;
    margin-bottom: 0;
		padding: 0 10%;
	}
}

.slide--item {
	overflow: hidden !important;

	.collection-item.big-pic {
		position: relative;
		height: 70vh;
		width: 50vw;

		.collection--img {
			left: -2%;
			position: absolute;
			height: 100%;
			width: 50vw;

			img {
				object-fit: cover;
				max-width: 100%;
				min-height: 100%;
				z-index: 0;
			}
			
			.img-overlay {
				backdrop-filter: brightness(80%) blur(0.5px);		
				-webkit-backdrop-filter: brightness(80%) blur(0.5px);
				position: absolute;
				inset: 0;
				background-color: rgba($color: #111, $alpha: 0.3);
				transition: all 0.3s ease ;
			}

		}

		.collection--content {
			position: absolute;
			right: auto;
			text-align: left;
			top: 50%;
			right: -190px;
			max-width: 340px;
			z-index: 2;
			-webkit-transform: translateY(-50%);
    	transform: translateY(-50%);
			z-index: 2;
			-moz-transform: translateY(-50%);

			.collection--title h3 {
				font-family: "Montserrat", sans-serif;
				font-size: 48px;
				font-weight: 700;
				line-height: 1.5;
				transition: 0.3s ease;

				&:hover {
					color: #F26C4F !important;
				}
			}

			.collection--desc p {
				font-family: "Lato", sans-serif;
				font-size: 14px;
				font-weight: 400;
				color: #FFFFFF;
				line-height: 1.5;
			}
		}

		&:hover {

			.collection--img .img-overlay {
				backdrop-filter: brightness(90%) blur(0);		
				-webkit-backdrop-filter: brightness(90%) blur(0);
			}
		}
	}

	.collection-item.product-side {
		position: relative;
		text-align: center;
		height: 100%;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;

		.collection--img {
			width: 100%;
			margin-bottom: 20px;
			display: flex;
			justify-content: center;

			img {
				max-width: 50%;
				max-height: 300px;
			}
		}

		.collection--content {

			.collection--title h3 {
				font-family: "Montserrat", sans-serif;
				font-size: 18px;
				line-height: 1;
				font-weight: 700;
				color: #a1a1a1;
				border: none !important;

				&:hover {
					color: #F26C4F !important;
				}

				&:after {
					display: none !important;
				}
			}

			.collection--price {
				color: #A1A1A1;
				font-size: 16px;
				font-weight: 700;
				line-height: 1.5;
			}
		}
	}
}

@media ( max-width: 999px ) {
	.features-carousel {
		.feature-item {
			flex-direction: column !important;
			align-items: center;
		}
	}
}

@media ( max-width: 768px ) {

	.features-carousel {
		padding-bottom: 0;
	}
	
	.featured-collection-container {
	
		h2.heading--title {
			font-size: 30px;
		}

		.heading--desc {
			font-size: 18px !important;
		}

	}

	.slide--item {
	
		.collection-item.big-pic {
			height: 550px;
			width: 100%;
	
			.collection--img {
				width: 100%;
				left: 0;
			}
	
			.collection--content {
				left: 50%;
				top: 25%;
				transform: translateX(-50%);

			}

		}

		.collection-item.product-side {
			position: relative;
			text-align: center;
			height: 400px;
			display: flex;
			flex-direction: column;
			justify-content: center;
			align-items: center;
			padding: 50px 0 0 ;
	
			.collection--img {
				width: 100%;
				margin-bottom: 20px;
				display: flex;
				justify-content: center;
	
				img {
					width: auto;
					height: auto;
				}
			}
	
			.collection--content {
	
			}
		}
	}

	.product-side {
		display: none !important;
	}
}

@media ( max-width: 600px ) {
	.featured-collection-container {

		.heading--desc {
			font-size: 0.93rem !important;
			width: 100%;
			line-height: 1.2;
			padding: 0 6%;
		}
	}
}